@use '../base/variables' as v;
@use '../base/mixsins' as m;


html{
    background-color: rgb(194, 189, 189);

}

.nosotros-page{
    height: 12vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(../../../build/img/fondo1.webp);
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    

}

.nosotros-post{
    height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(../../../build/img/fondo-madera.webp);
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
}


.container-nosotros{
    display: flex;
    flex-direction: row;

    .container {
        display: flex;
        justify-content: space-between;
    }
    .content {
        width: 65%;
    }
    .sidebar {
        width: 30%;
    }
    h1 {
        color: #1E70B8;
        font-size: 2em;
        margin-bottom: 20px;
        

    }
    h2 {
        color: #1E70B8;
        margin-bottom: 10px;

    }
    .section-title {
        font-weight: bold;
        font-size: 1.1em;
    }
    .section-content {
        margin-bottom: 20px;
    }
    .sidebar img {
        width: 100%;
        margin-bottom: 20px;
    }
    margin: 0 2rem;
    text-align: justify;
    line-height: 1.5;
    position: relative;
    z-index: 1;
    padding: 2rem;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 10px;




    h1,
    h2,
    p,
    li,
    span {
        margin-left: 5rem;
        font-size: 1.1em;
        
    }

    .banner{
        width: 700px;
        height: auto;
    }
    .banner:hover{
        transform: scale(1.1);
        transition: 0.8s;
    }
}

.sidebar{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1rem;
    margin-left: 5rem;
    margin-right: 5rem;
}



@media (max-width: 768px) {
    .container-nosotros {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 1rem;
        text-align: start;
        padding: 1rem;
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: 10px;
    }

    .content {
        width: 100%;
        margin-bottom: 1rem;
    }

    .sidebar {
        width: 100%;
        margin: 0;
    }

    h1, h2 {
        margin-left: 0;
        font-size: 1.5em;
        text-align: center;
    }

    p, li {
        margin-left: 0;
        font-size: 1em;
        text-align: justify;
        line-height: 1.6;
    }

    .section-content {
        margin-bottom: 1rem;
    }

    .banner img {
        width: 100%;
        height: auto;
    }

    .nosotros-page {
        height: auto;
        padding: 2rem 0;
        text-align: center;
    }
}

@media (max-width: 480px) {
    .container-nosotros {
        padding: 1rem;
    }

    h1, h2 {
        font-size: 1.3em;
    }

    p, li {
        font-size: 0.9em;
        line-height: 1.4;
    }

    .banner img {
        width: 100%;
        height: auto;
    }
}
