@use '../base/variables' as v;
@use '../base/mixsins' as m;
.contenedor-inicio{
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-image: url('/build/img/imagen-quesos-2.webp');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    flex-direction: column;
   
    h1{
        color: v.$blanco;
        font-size: 10rem;
        margin: 2rem;
    }
    p{
        color: v.$blanco;
        font-size: 3rem;
    }


}

.contenedor-inicio::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Ajusta el color y la opacidad según sea necesario */
    z-index: 1; /* Asegúrate de que el pseudo-elemento esté detrás del contenido */
}

.contenedor-inicio > * {
    position: relative; /* Asegúrate de que el contenido esté por encima del pseudo-elemento */
    z-index: 2;
}

@media (max-width: 1100px) {
    .contenedor-inicio{
        h1{
            font-size: 5rem;
        }
        p{
            font-size: 2rem;
        }
    }
}

@media (max-width: 768px) {
    .contenedor-inicio{
        h1{
            font-size: 4rem;
        }
        p{
            font-size: 2rem;
            margin: 1rem;
        }
    }
}

@media screen and (max-width: 400px) {
    .contenedor-inicio::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.5); /* Ajusta el color y la opacidad según sea necesario */
        z-index: 1; /* Asegúrate de que el pseudo-elemento esté detrás del contenido */
    }
}