@use '../base/variables' as v;
@use '../base/mixsins' as m;
.footer{
    width: 100%;
    height: 35vh;
    background-color: v.$azul-oscuro;
}

.contenedor-footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    p{
        color: v.$blanco;

        font-weight: 400;
        a{
            color: v.$azul;       
        }
    }
    .imagen-footer{
        width: 15rem;
        height: 15rem;
    }
}

.redes {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    
    .redes__link {
        color: white;
        font-size: 2rem; /* Aumenta el tamaño de la fuente para que los iconos sean más grandes */
        margin: 1rem; /* Ajusta el espaciado entre los iconos */
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.1); /* Fondo semi-transparente */
        width: 5rem; /* Ancho fijo para hacer los iconos circulares */
        height: 5rem; /* Alto fijo para hacer los iconos circulares */
        display: flex;
        align-items: center;
        justify-content: center;
        transition: background-color 0.3s, color 0.3s;
        text-align: center;

        &:hover {
            color: blue; /* Color de texto al pasar el ratón */
            background-color: rgba(255, 255, 255, 0.3); /* Fondo más claro al pasar el ratón */
        }
        
        i {
            font-size: 2rem; /* Ajusta el tamaño del icono dentro del link */
            text-align: center;
        }
    }
}

