@use '../base/variables' as v;
@use '../base/mixsins' as m;
.contacto{
    width: 100%;
    height: 100vh;
    background-color: rgba(243, 243, 243, 0.856);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}


.contacto > * {
    position: relative; /* Asegúrate de que el contenido esté por encima del pseudo-elemento */
    z-index: 2;
}

.contacto-container{
    padding: 10rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    .map{
        height: 40vh;
        width: 120vh;
    }
    h1{
        margin-bottom: 11rem;
    }
}

.info-contacto-container{
    width: 120vh;
    
    p{
        color: v.$negro;
        font-size: 1.8rem;
        font-weight: 500;

    }
    .numero{
        font-weight: 800;
    }
    .mail{
        color: v.$azul;
    }
}

@media (max-width: 1350px) {
    .contacto-container{
        .map{
            height: 30vh;
            width: 90vh;
        }
    }

    .info-contacto-container{
        width: 90vh;
        p{
            font-size: 1.7rem;
        }
    }
}

@media (max-width: 1025px) {
    .contacto-container{
        .map{
            height: 30vh;
            width: 60vh;
        }
    }

    .info-contacto-container{
        width: 60vh;
        p{
            font-size: 1.5rem;
            text-align: justify;
        }
    }
}

@media (max-width: 700px) {
    .contacto-container{
        .map{
            height: 30vh;
            width: 30vh;
        }
    }

    .info-contacto-container{
        width: 30vh;
        margin-top: 11rem;
        p{
            text-align: justify;
            
        }
    }
}