@use '../base/variables' as v;
@use '../base/mixsins' as m;



.header {
    display: flex;
    justify-content: space-around;
    position: fixed;
    width: 100%;
    box-shadow: .1rem .2rem .3rem rgba(0, 0, 0, .5);
    z-index: 100;
    background-color: v.$blanco;
    display: none; /* Ocultar por defecto */

    .imagen-logo {
        width: 10rem;
        height: 10rem;
        margin-right: 10rem;
        
        cursor: pointer;

        &:hover {
            transition: 0.2s;
        }
    }

    .abrir-menu,
    .cerrar-menu {
        display: none;
    }

    @media (max-width: 1050px) {
        .abrir-menu,
        .cerrar-menu {
            display: block;
            border: 0;
            background-color: transparent;
            color: v.$negro;
            font-size: 3rem;
            cursor: pointer;
        }

        .navegacion-principal {
            opacity: 0;
            visibility: hidden;
            display: flex;
            align-items: start;
            gap: 1rem;
            flex-direction: column;
            right: 0;
            position: fixed;
            color: v.$azul-oscuro;
            align-items: start;
            background-color: v.$blanco;
            padding: 2rem;
            box-shadow: 0 0 0 100vmax rgba(0, 0, 0, .5);
            height: 100vh;
            width: 50%;

            transform: translateX(100%);

            a {
                color: v.$azul-oscuro;
            }

            button {
                color: v.$azul-oscuro;
            }

            i {
                color: v.$azul-oscuro;
            }
        }

        .nav-visible {
            opacity: 1;
            visibility: visible;
            transition: .5s;
            transform: translateX(0);
        }
    }
}

.abrir-menu {
    display: none;
}

.navegacion-principal {
    .nav__link__active {
        border-bottom: 2px solid v.$azul;
    }

    @include m.tablet {
        display: flex;
        gap: 5rem;
        align-items: center;
    }

    button {
        border: 0;
        background-color: transparent;
        color: v.$negro;
        font-size: 1.5rem;
        cursor: pointer;
        font-weight: 500;

        &:hover {
            border-bottom: 2px solid v.$rojo;
        }
    }

    a {
        transform: scale(1);
        display: block;
        text-align: center;
        color: v.$azul-oscuro;
        font-size: 2rem;
        margin-bottom: 1rem;
        padding: 0.5rem 0;
        font-weight: 500;

        @include m.tablet {
            margin: 0;
        }

        &:hover {
            border-bottom: 2px solid v.$azul;
        }
    }
}

.header.show {
    display: flex;
    justify-content: center;
    a{
        color: v.$azul-oscuro;
    }
    
}

// Header1 
.header.show1{
    display: flex;
    justify-content: center;
    background-color: transparent;
    a{
        color: rgba(255, 255, 255, 0.459);
        margin-left: 5rem;
        margin-right: 5rem;
        font-size: 2.5rem;
    }
    a:hover{
        color: v.$azul;
    }
    justify-content: space-evenly;

    .imagen-logo{
        width: 20rem;
