        background-color: rgba(0, 0, 0, 0.8);
    }
    box-shadow: .1rem .2rem .3rem rgba(0, 0, 0, .0);
    .nav__link__active {
        border-style: none;
        color: rgba(255, 255, 255, 0.973);
    }
    .navegacion-principal{
        a{
            font-size: 2rem;
            margin: 0 auto;
            &:hover{
