            padding: 2rem;
            box-shadow: 0 0 0 100vmax rgba(0, 0, 0, .5);
            height: 100vh;
            width: 50%;

            transform: translateX(100%);

            a {
                color: v.$azul-oscuro;
            }

            button {
                color: v.$azul-oscuro;
            }

