@use '../base/variables' as v;
@use '../base/mixsins' as m;
.sobre-nosotros {
    width: 100%;
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
   background-image: url(../../../build/img/fondo1.webp);
   background-size: cover;
   background-attachment: fixed;
   background-repeat: no-repeat;
}

.contenedor-info-sobre-nosotros {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}


.imagen-sobre-nosotros {
    width: 55vh; 
    height: auto;
    margin-right: 30vh;
}

.texto-sobre-nosotros {
    max-width: 600px; /* Ajusta el ancho máximo según sea necesario */
}

.texto-sobre-nosotros h1 {
    margin-bottom: 1rem;
    color: v.$azul-oscuro;
    font-size: 4rem;
}

.texto-sobre-nosotros p {
    color: v.$negro;
    font-size: 2.5rem;
}
.boton-sobre-nosotros {
    background-color: v.$rojo;
    color: v.$blanco;
    padding: 1rem 2rem;
    border: none;
    border-radius: 25px;
    font-size: 2rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    &:hover {
        background-color: v.$blanco;
        color: v.$rojo;
    }

}


.sobre-nosotros > * {
    position: relative; /* Asegúrate de que el contenido esté por encima del pseudo-elemento */
    z-index: 2;
}
@media (max-width: 1394px) {
    .contenedor-info-sobre-nosotros {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
    }
    .imagen-sobre-nosotros {
        margin: 0;
        margin-bottom: 10rem;
    }
    .slider-indicators.special {
        top: 265px;
        margin: 0 auto;
    }

}

@media (max-width: 1600px) {
    .texto-sobre-nosotros h1 {
        font-size: 3rem;
        text-align: justify;
    }
    .texto-sobre-nosotros p {
        font-size: 2rem;
        text-align: justify;
    }


    .boton-sobre-nosotros {
        font-size: 1.5rem;
    }
    .imagen-sobre-nosotros {
        width: 40vh;
        height: auto;
    }
    .slider-indicators.special {
        text-align: center;
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
    }
}

@media (max-width: 768px) {
    .contenedor-info-sobre-nosotros {
        margin: 1rem;
    }
    
    .imagen-sobre-nosotros {
        width: 30vh;
        height: auto;
    }
    .texto-sobre-nosotros h1 {
        margin-left: 15px;
        font-size: 1.8rem;
        text-align: justify;
    }
    .texto-sobre-nosotros p {
        margin: 2rem;
        font-size: 1.6rem;
        text-align: justify;
    }

    .boton-sobre-nosotros {
        font-size: 1.2rem;
        margin: 1rem;
    }
    .slider-indicators.special3 {
        text-align: center;
        position: absolute;
        top: 180px;
        left: 85%;
        transform: translateX(-50%);
    }   
}

.slider {
    position: relative;

    overflow: hidden;
}

.slide {
    display: none;
    width: 100%;
    height: auto;
}

.slide.active {
    display: block;
}

.slider-indicators {
    text-align: center;
    position: absolute;
    bottom: 10px;
    left: 60%;
    transform: translateX(-50%);
}

.indicator {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 0 5px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    cursor: pointer;
}

.indicator.active {
    background-color: red;
}
