@use '../base/variables' as v;
@use '../base/mixsins' as m;






.contenedor-info-sobre-reparto {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;


}

.texto-sobre-reparto {
    h1{
        margin-bottom: 1rem;
        color: v.$azul-oscuro;
        font-size: 4rem;
    }   
    p{
        color: v.$negro;
        font-size: 2.5rem;
    }
}


.imagen-sobre-reparto{
    width: 500px; 
    margin-left: 30vh;
    height: auto;
    border-radius: 10px;
}

@media screen and (max-width: 1460px) {
    .texto-sobre-reparto {
        h1{
            font-size: 3.2rem;
        }   
        p{
            font-size: 2rem;
        }
    }
    
    
    .imagen-sobre-reparto{
        width: 400px; 
    }
    
}


@media screen and (max-width: 1200px) {
    .contenedor-info-sobre-reparto {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .texto-sobre-reparto {
        h1{
            font-size: 3.2rem;
            margin-bottom: 3rem;
        }   
        p{
            font-size: 2rem;
            margin-top: 1rem;
        }
    }
    
    
    .imagen-sobre-reparto{
        margin-left: 0;
        margin-top: 5rem;
        width: 300px;
    }
    
}


@media screen and (max-width: 500px) {

    .texto-sobre-reparto {
        h1{
            font-size: 3rem;
            margin-bottom: 3rem;
        }   
        p{
            font-size: 1.6rem;
            margin-top: 1rem;
        }
    }
    
    
    
}




